
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html,
body {
  background-color: #4fc08d;
  height: 100%;
  font-size: 14px;
}
input {
  border: 1px solid #fff;
  outline: 0; /* 去掉input 聚焦时边框周围黄色 */
}
li {
  list-style: none;
}

.todo[data-v-104fcbf2] {
  width: 90%;
  height: 100%;
  margin: 0 auto;
  color: #fff;
  padding-bottom: 90px;
}
.add-box[data-v-104fcbf2] {
  padding: 20px 0;
}
.add-todo[data-v-104fcbf2] {
  display: block;
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-radius: 20px;
  padding-left: 20px;
}
.main h3[data-v-104fcbf2] {
  color: #fff;
  line-height: 2em;
  padding-left: 1em;
  font-size: 0.9em;
  font-weight: normal;
  margin-bottom: 0.5em;
}
.main li[data-v-104fcbf2] {
  background-color: #fff;
  color: #4fc08d;
  line-height: 2em;
  padding-left: 1em;
  margin-bottom: 0.28em;
  -webkit-transition: all ease-in-out .2s;
  transition: all ease-in-out .2s;
}
.main li input[type='checkbox'][data-v-104fcbf2] {
  vertical-align: middle;
  margin-right: 4px;
}
.active-todos li[data-v-104fcbf2]:hover {
  background-color: #eee;
}
.completed-todos[data-v-104fcbf2] {
  margin-top: 20px;
}
.completed-todos li[data-v-104fcbf2] {
  position: relative;
  background-color: #ccc;
  color: #999;
  text-decoration: line-through;
}
.list-empty[data-v-104fcbf2] {
  text-align: center;
  font-size: 0.9em;
}
.completed-todos li .remove-todo[data-v-104fcbf2] {
  position: absolute;
  right: 0;
  top: 0;
  height: 2em;
  line-height: 2em;
  padding: 0 10px;
  display: none;
  cursor: pointer;
}
.completed-todos li:hover .remove-todo[data-v-104fcbf2] {
  display: block;
}
/* action */
.footer[data-v-104fcbf2] {
  position: fixed;
  width: 100%;
  padding: 1em 0;
  bottom: 0;
  left: 0;
  background-color: #4fc08d;
  opacity: 0.85;
}
.action[data-v-104fcbf2] {
  text-align: center;
  padding-bottom: 1em;
}
.action span[data-v-104fcbf2] {
  cursor: pointer;
  margin: 0 5px;
  font-size: .9em;
}
